"use client";

import { useState, useEffect } from "react";
import Image from "next/image"; // optional; change to <img> if not using next/image
import { events } from "../data/eventsData";

export default function Events() {
  const [open, setOpen] = useState(null); // event id or null

  useEffect(() => {
    function handler(e) {
      if (e.key === "Escape") setOpen(null);
    }
    window.addEventListener("keydown", handler);
    return () => window.removeEventListener("keydown", handler);
  }, []);

  return (
    <section className="py-16 bg-transparent px-4">
      <div className="max-w-6xl mx-auto">
        <h2 className="text-4xl font-cinzelDecorative text-white text-center mb-8">
          Events Timeline
        </h2>

        <ul className="relative border-l border-neutral-700 ml-4">
          {events.map((ev, idx) => {
            const isLeft = idx % 2 === 0; // alternate sides
            return (
              <li key={ev.id} className="mb-12 relative">
                {/* Dot + date */}
                <div className="absolute -left-8 top-2 flex flex-col items-center">
                  <div className="w-4 h-4 rounded-full bg-accent border-2 border-black" />
                  <div className="text-xs text-neutral-300 mt-2 text-center">
                    <div>{new Date(ev.date).toLocaleDateString()}</div>
                    <div className="mt-1 font-semibold">{ev.time}</div>
                  </div>
                </div>

                {/* Content box */}
                <div
                  className={`timeline-box w-full md:w-1/2 p-4 rounded-lg bg-neutral-900/60 border border-neutral-700 shadow-md ${
                    isLeft ? "md:ml-8 md:mr-auto md:translate-x-0" : "md:ml-auto md:mr-8"
                  }`}
                >
                  <div className="flex md:flex-row flex-col items-start gap-4">
                    {/* Poster thumbnail */}
                    <button
                      onClick={() => setOpen(ev.id)}
                      className="w-40 h-28 flex-shrink-0 overflow-hidden rounded-md border border-neutral-600"
                      aria-label={`Open details for ${ev.title}`}
                    >
                      {/* Use next/image if desired for optimization */}
                      <img
                        src={ev.poster}
                        alt={ev.title}
                        className="w-full h-full object-cover transform hover:scale-105 transition"
                        draggable={false}
                      />
                    </button>

                    <div className="flex-1">
                      <h3 className="text-xl font-semibold text-white">{ev.title}</h3>
                      <p className="text-sm text-neutral-300 mt-1">{ev.short}</p>

                      <div className="mt-3 flex items-center gap-3">
                        <span className="text-xs text-neutral-400">Date:</span>
                        <span className="text-sm text-white font-medium">
                          {new Date(ev.date).toLocaleDateString()}
                        </span>

                        <span className="text-xs text-neutral-400 ml-4">Time:</span>
                        <span className="text-sm text-white font-medium">{ev.time}</span>
                      </div>

                      <div className="mt-4">
                        <button
                          onClick={() => setOpen(ev.id)}
                          className="px-3 py-1 rounded bg-accent text-black font-semibold"
                        >
                          View Details
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </li>
            );
          })}
        </ul>
      </div>

      {/* Modal / Drawer for event details */}
      {open && (
        <EventModal
          eventData={events.find((e) => e.id === open)}
          onClose={() => setOpen(null)}
        />
      )}
    </section>
  );
}

/* Modal component inside same file for convenience */
function EventModal({ eventData, onClose }) {
  useEffect(() => {
    // lock scroll
    document.body.style.overflow = "hidden";
    return () => {
      document.body.style.overflow = "";
    };
  }, []);

  if (!eventData) return null;

  return (
    <div
      className="fixed inset-0 z-50 flex items-end md:items-center justify-center"
      aria-modal="true"
      role="dialog"
    >
      {/* overlay */}
      <div
        className="absolute inset-0 bg-black/70 backdrop-blur-sm"
        onClick={onClose}
      />

      {/* panel */}
      <div className="relative bg-neutral-900 rounded-t-lg md:rounded-lg w-full md:max-w-4xl mx-4 md:mx-0 overflow-hidden">
        <div className="md:flex">
          {/* poster */}
          <div className="md:w-1/2 h-64 md:h-auto relative">
            <img
              src={eventData.poster}
              alt={eventData.title}
              className="w-full h-full object-cover"
              draggable={false}
            />
          </div>

          {/* details */}
          <div className="p-6 md:w-1/2">
            <div className="flex items-start justify-between">
              <div>
                <h3 className="text-2xl font-semibold text-white">{eventData.title}</h3>
                <p className="text-sm text-neutral-300 mt-1">
                  {new Date(eventData.date).toLocaleDateString()} • {eventData.time}
                </p>
              </div>
              <button
                onClick={onClose}
                className="text-neutral-300 hover:text-white"
                aria-label="Close"
              >
                ✕
              </button>
            </div>

            <p className="mt-4 text-neutral-200">{eventData.description}</p>

            <div className="mt-6 flex gap-3">
              <button
                className="px-4 py-2 bg-accent text-black font-semibold rounded"
                onClick={() => {
                  // example action: navigate to registration (implement as needed)
                  window.alert("Register action (implement)");
                }}
              >
                Register
              </button>

              <button
                className="px-4 py-2 border border-neutral-700 text-neutral-200 rounded"
                onClick={onClose}
              >
                Close
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
